* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: roboto;
}

ol,
ul,
li {
    list-style: none;
}

html,
body {
    height: 100%;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
}

.form-layout {
    display: flex;
    border: 6px solid rgb(38 132 255 / 41%);
    width: 1000px;
    border-radius: 20px;
    overflow: hidden;
}

.navigation-column {
    flex-basis: 314px;
    padding: 60px 50px 60px 20px;
    color: #fff;
    background-color: #252B42;
    font-size: 20px;
}

.form-column {}

.form-column {
    flex-grow: 1;
    padding: 60px 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #3a9c78;
    background-color: rgba(11, 42, 179, 0.658);
    color: #fff;
    font-size: 15px;
    font-weight: 500;
    position: relative;
}


.list-item {
    padding: 30px 5px;
    display: flex;
    align-items: center;
    column-gap: 15px;
    position: relative;
    opacity: 0.5;
}

.list-item:last-child {
    padding-bottom: 0;
}

.list-item:not(:last-child)::before {
    position: absolute;
    content: "";
    background-color: #fff;
    width: 2px;
    height: 30px;
    left: 20px;
    bottom: -13px;

}

.list-item.current {
    opacity: 1;
}

.item-marker {
    border: 2px solid white;
    border-radius: 50%;
    padding: 2px 9px;
}

.item-text {
    white-space: nowrap;
}



.form-scheme-toggler {
    position: absolute;
    background: none;
    border: none;
    outline: none;
    right: 40px;
    top: 25px;
    cursor: pointer;
}

.form-scheme-toggler .dark {
    display: none;
}

.form-input-field {
    display: flex;
    flex-direction: column;
    gap: 5px 10px;
}

.form-input-field label {
    font-size: 14px;
}

.form-input-field input {
    border: 2px solid #E6E6E6;
    color: #fff;
    background: transparent;
    border-radius: 5px;
    padding: 10px;
    outline: none;
    font-size: 15px;
    font-weight: 500;

}

.page-form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    column-gap: 10px;
    row-gap: 25px;
}

.page-form>* {
    flex-basis: 45%;
}

.form-nav {
    display: flex;
    column-gap: 20px;
}

.btn {
    appearance: none;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 15px;
    font-weight: 500;
    padding: 10px 20px;
    background: #f0f8ff;
    border-radius: 10px;
    transition: 0.5s;
}

.btn:hover {
    color: rgba(11, 42, 179, 0.658);
}

.btn.disabled {
    opacity: 0.6;
    cursor: auto;
    pointer-events: none;
}

.btn.disabled:hover {
    color: #000;
}


.btn:last-child {
    padding: 10px 20px;
    background: #f0f8ff;
    border-radius: 10px;
}

.on-form-submit {
    display: flex;
    flex-direction: column;
    row-gap: 25px;
    align-items: center;
    justify-content: center;
    height: 100%;

}