.light .navigation-column {
    background-color: #2684ff69;
    color: #252B42;
}

.light .form-column {
    background-color: #FAFAFA;
    color: #252B42;
}

.light .btn,
.light .btn:hover,
.light .btn.disabled,
.light .btn.disabled:hover {
    background-color: #252B42;
    color: #FAFAFA;
}

.light .btn:hover {
    opacity: 0.8;
}

.light .btn.disabled:hover {
    opacity: 0.6;
}

.light input {
    color: #252B42 !important;
}

.light .form-scheme-toggler .light {
    display: none;
}

.light .form-scheme-toggler .dark {
    display: unset;
}